"use client"

import * as React from "react"
import { useState } from "react"
import { cn } from "@/lib/utils"
import { Truck, Users, UserCog, FileText, Calendar, Zap, FlaskConical, Shield, Key, Building2, CreditCard } from "lucide-react"

interface NuvioCoreDemoProps {
    mode?: "compact" | "full"
    className?: string
}

// Core Responsibilities
const CORE_SERVICES = [
    { id: "auth", name: "Auth", icon: Shield },
    { id: "roles", name: "Roles & Permissions", icon: Key },
    { id: "tenants", name: "Tenants", icon: Building2 },
    { id: "billing", name: "Billing", icon: CreditCard },
]

// Product Modules (Branches)
const PRODUCTS = [
    {
        id: "fleet",
        name: "NuVio Fleet",
        icon: Truck,
        color: "bg-blue-500",
        manages: "Vehicle management, contracts, damage tracking, PDF handovers",
        dependencies: ["Auth", "Roles", "Docs"]
    },
    {
        id: "clients",
        name: "NuVio Clients",
        icon: Users,
        color: "bg-green-500",
        manages: "Customer management, invoices, payments, customer documents",
        dependencies: ["Auth", "Roles", "Billing", "Docs"]
    },
    {
        id: "staff",
        name: "NuVio Staff",
        icon: UserCog,
        color: "bg-purple-500",
        manages: "Employees, roles, access control, activity overview",
        dependencies: ["Auth", "Roles"]
    },
    {
        id: "docs",
        name: "NuVio Docs",
        icon: FileText,
        color: "bg-amber-500",
        manages: "Documents, PDFs, exports, shared across products",
        dependencies: ["Auth", "Tenants"]
    },
    {
        id: "plan",
        name: "NuVio Plan",
        icon: Calendar,
        color: "bg-cyan-500",
        manages: "Scheduling, calendars, workflows",
        dependencies: ["Auth", "Staff"]
    },
    {
        id: "edge",
        name: "NuVio Edge",
        icon: Zap,
        color: "bg-orange-500",
        manages: "Background jobs, OCR, automation, integrations",
        dependencies: ["Auth", "Tenants"]
    },
    {
        id: "lab",
        name: "NuVio Lab",
        icon: FlaskConical,
        color: "bg-pink-500",
        manages: "Experiments, prototypes, beta features",
        dependencies: ["Auth"]
    },
]

export function NuvioCoreDemo({ mode = "full", className }: NuvioCoreDemoProps) {
    const isCompact = mode === "compact"
    const [selectedProduct, setSelectedProduct] = useState<string | null>(null)
    const [hoveredProduct, setHoveredProduct] = useState<string | null>(null)

    // Compact mode shows only key products
    const displayProducts = isCompact
        ? PRODUCTS.filter(p => ["fleet", "clients", "staff", "docs"].includes(p.id))
        : PRODUCTS

    const selectedProductData = PRODUCTS.find(p => p.id === selectedProduct)

    return (
        <div className={cn("flex flex-col bg-background text-foreground h-full w-full", isCompact ? "select-none pointer-events-none" : "border rounded-xl shadow-sm", className)}>
            {/* Platform Header */}
            <div className="flex items-center justify-between border-b px-4 py-3 bg-muted/20">
                <div className="flex items-center gap-2">
                    <div className="h-3 w-3 rounded-full bg-purple-500" />
                    <span className="text-xs font-medium font-mono text-muted-foreground">NUVIO PLATFORM</span>
                </div>
                {!isCompact && (
                    <div className="flex gap-2">
                        <span className="text-xs px-2 py-1 rounded bg-muted/50 text-muted-foreground font-mono">
                            {PRODUCTS.length} Products
                        </span>
                    </div>
                )}
            </div>

            {/* Main Ecosystem Visualization */}
            <div className={cn("flex-1 flex items-center justify-center p-6", isCompact ? "overflow-hidden" : "overflow-y-auto")}>
                <div className="w-full max-w-5xl">

                    {/* Ecosystem Layout */}
                    <div className="relative">

                        {/* Central Core */}
                        <div className="flex justify-center mb-8">
                            <div className={cn(
                                "relative rounded-2xl border-2 border-purple-500 bg-purple-500/10 backdrop-blur-sm",
                                isCompact ? "p-4" : "p-6"
                            )}>
                                <div className="text-center space-y-2">
                                    <div className="flex items-center justify-center gap-2">
                                        <div className="h-3 w-3 rounded-full bg-purple-500 animate-pulse" />
                                        <h3 className={cn("font-bold text-purple-600 dark:text-purple-400", isCompact ? "text-sm" : "text-lg")}>
                                            NuVio Core
                                        </h3>
                                    </div>
                                    {!isCompact && (
                                        <div className="flex gap-2 justify-center flex-wrap">
                                            {CORE_SERVICES.map((service) => {
                                                const Icon = service.icon
                                                return (
                                                    <div key={service.id} className="flex items-center gap-1 text-[10px] px-2 py-1 rounded bg-purple-500/20 text-purple-700 dark:text-purple-300 font-medium">
                                                        <Icon className="w-3 h-3" />
                                                        <span>{service.name}</span>
                                                    </div>
                                                )
                                            })}
                                        </div>
                                    )}
                                </div>

                                {/* Connection Lines to Products */}
                                {!isCompact && displayProducts.map((product, index) => {
                                    const angle = (index / displayProducts.length) * 2 * Math.PI - Math.PI / 2
                                    const distance = 180
                                    const x = Math.cos(angle) * distance
                                    const y = Math.sin(angle) * distance

                                    return (
                                        <svg
                                            key={product.id}
                                            className="absolute top-1/2 left-1/2 pointer-events-none"
                                            style={{
                                                width: Math.abs(x) * 2 + 20,
                                                height: Math.abs(y) * 2 + 20,
                                                transform: `translate(-50%, -50%)`,
                                                overflow: 'visible'
                                            }}
                                        >
                                            <line
                                                x1="50%"
                                                y1="50%"
                                                x2={x > 0 ? "100%" : "0%"}
                                                y2={y > 0 ? "100%" : "0%"}
                                                stroke="currentColor"
                                                strokeWidth="2"
                                                strokeDasharray="4 4"
                                                className={cn(
                                                    "transition-all duration-300",
                                                    hoveredProduct === product.id
                                                        ? "text-purple-500 opacity-100"
                                                        : "text-border opacity-30"
                                                )}
                                            />
                                        </svg>
                                    )
                                })}
                            </div>
                        </div>

                        {/* Product Modules arranged in circle */}
                        <div className="relative" style={{ minHeight: isCompact ? '200px' : '400px' }}>
                            {displayProducts.map((product, index) => {
                                const Icon = product.icon
                                const angle = (index / displayProducts.length) * 2 * Math.PI - Math.PI / 2
                                const distance = isCompact ? 100 : 180
                                const x = Math.cos(angle) * distance
                                const y = Math.sin(angle) * distance

                                return (
                                    <div
                                        key={product.id}
                                        className={cn(
                                            "absolute rounded-lg border bg-card transition-all duration-300",
                                            isCompact ? "p-2 w-20" : "p-4 w-36 cursor-pointer hover:scale-105 hover:shadow-lg",
                                            hoveredProduct === product.id && "ring-2 ring-purple-500 shadow-lg scale-105",
                                            selectedProduct === product.id && "ring-2 ring-purple-500"
                                        )}
                                        style={{
                                            left: '50%',
                                            top: '50%',
                                            transform: `translate(calc(-50% + ${x}px), calc(-50% + ${y}px))`
                                        }}
                                        onMouseEnter={() => !isCompact && setHoveredProduct(product.id)}
                                        onMouseLeave={() => !isCompact && setHoveredProduct(null)}
                                        onClick={() => !isCompact && setSelectedProduct(product.id)}
                                    >
                                        <div className="flex flex-col items-center text-center space-y-2">
                                            <div className={cn("rounded-full p-2", product.color, "bg-opacity-20")}>
                                                <Icon className={cn("text-current", isCompact ? "w-4 h-4" : "w-6 h-6")} style={{ color: product.color.replace('bg-', '') }} />
                                            </div>
                                            <div className={cn("font-semibold", isCompact ? "text-[10px]" : "text-xs")}>
                                                {product.name}
                                            </div>
                                        </div>
                                    </div>
                                )
                            })}
                        </div>
                    </div>

                    {/* Detail Panel (Full Mode Only) */}
                    {!isCompact && selectedProductData && (
                        <div className="mt-8 rounded-lg border bg-card p-6 animate-in slide-in-from-bottom-4">
                            <div className="flex items-start justify-between mb-4">
                                <div className="flex items-center gap-3">
                                    {React.createElement(selectedProductData.icon, {
                                        className: cn("w-8 h-8", selectedProductData.color.replace('bg-', 'text-'))
                                    })}
                                    <div>
                                        <h4 className="text-lg font-bold">{selectedProductData.name}</h4>
                                        <p className="text-sm text-muted-foreground mt-1">{selectedProductData.manages}</p>
                                    </div>
                                </div>
                                <button
                                    onClick={() => setSelectedProduct(null)}
                                    className="text-xs text-muted-foreground hover:text-foreground transition-colors"
                                >
                                    Close
                                </button>
                            </div>
                            <div className="pt-4 border-t">
                                <div className="text-xs font-medium text-muted-foreground mb-2">Depends on Core services:</div>
                                <div className="flex gap-2 flex-wrap">
                                    {selectedProductData.dependencies.map((dep) => (
                                        <span key={dep} className="text-xs px-2 py-1 rounded bg-purple-500/10 text-purple-600 dark:text-purple-400 font-medium">
                                            {dep}
                                        </span>
                                    ))}
                                </div>
                            </div>
                        </div>
                    )}

                    {/* Hint Text (Full Mode Only) */}
                    {!isCompact && !selectedProduct && (
                        <div className="mt-8 text-center">
                            <p className="text-xs text-muted-foreground">
                                Click on a product module to view details
                            </p>
                        </div>
                    )}
                </div>
            </div>
        </div>
    )
}
